.order {
  display: block;
  width: 100%;
  background-color: #1c1c21;
  border-radius: 40px;
  padding: 24px;
  box-sizing: border-box;
  color: inherit;
  text-decoration: none;
}

.orderContainer:not(:last-child) {
  margin-bottom: 24px;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 24px;
}

.ingredientsContainer {
  display: flex;
}

.ingredientsList {
  display: flex;
  flex-direction: row-reverse;
  margin: 0;
  padding: 0;
  list-style: none;
}

.priceContainer {
  margin-top: 40px;
  margin-left: auto;
  display: flex;
  align-items: center;
}
